const reducer = (state = initState, action) => {
  switch (action.type) {
    case LOG_IN_REQUEST:
      // console.log(action.data)
      return {
        ...state,
        // isLoggedIn: true,
        // user: action.data
        // user: dummyUser
        isLoggingIn: true,
        loginError: ''
      };

    case LOG_IN_SUCCESS: {
      return {
        ...state,
        isLoggingIn: false,
        isLoggedIn: true,
        me: dummyUser,
      };
    }
    case LOG_IN_FAILURE: {
      return {
        ...state,
        isLoggingIn: false,
        isLoggedIn: false,
        loginError: action.error,
        me: null
      };
    }
    case LOG_OUT_REQUEST:
      return {
        ...state,
        isLoggedIn: false,
        user: null
      };
    case SIGN_UP_REQUEST:
      return {
        ...state,
        isSigningUp: true,
        isSignedUp: false,
        signUpError: ""
      };
    case SIGN_UP_SUCCESS:
      return {
        ...state,
        isSigningUp: false,
        isSignedUp: true
      };
    case SIGN_UP_FAILURE:
      return {
        ...state,
        isSigningUp: false,
        signUpError: action.error
      };

    default:
      return state;
  }
};

export default reducer;
